* {
  box-sizing: border-box;
  font-family: sans-serif;
}

html, body, #root {
  padding: 0;
  margin: 0;
  height: 100%;
}

.img-thumbnail {
  margin-bottom: 10px;
}

img {
  width: 135px;
}
.wrapper {
    padding-top: 10px;
    background: black;
    height: 100%;
    display: flex;
    flex-flow: row wrap;
    padding-left: 27%;
    padding-right: 27%;
    justify-content: space-around;
    align-content: flex-start;
    overflow: auto;
  }
  
.title {
    width: 100%;
    text-align: center;
    font-size: 2.5rem;
    color: red;
    margin-bottom: 2%;
    font-family: 'Bangers', cursive;
  }
.score {
    width: 100%;
    text-align: center;
    font-size: 1.5rem;
    color: red;
    margin-bottom: 2%;
    font-family: 'Bangers', cursive;
  }
